.portfolio__monitor {
  --he: 300px;
}

.portfolio__tablet {
  --he: 318px;
}

.portfolio__mobile {
  --he: 570px;
}

.contacts-block__title, .portfolio__title, .stage__title, .services__title {
  color: #f0b752;
  display: flex;
  justify-content: center;
  font-family: "Alumni Sans Pinstripe", sans-serif;
  font-size: 30px;
  letter-spacing: 3px;
  line-height: 30px;
  align-items: center;
  text-transform: uppercase;
  width: 100%;
  padding: 10px;
  margin: 40px auto;
  position: relative;
  border-top: 2px solid #f0b752;
}
.contacts-block__title::after, .portfolio__title::after, .stage__title::after, .services__title::after {
  content: "";
  min-width: 200px;
  height: 2px;
  position: absolute;
  bottom: 0;
  left: 50%;
  max-width: 500px;
  width: 100%;
  bottom: 0;
  transform: translateX(-50%);
  background: #f0b752;
  transition: all 0.5s;
}

.portfolio__button, .services__item-detail {
  display: block;
  margin: 15px;
  background: #f0b752;
  padding: 10px 30px;
  position: relative;
  min-width: 200px;
  text-align: center;
  border-radius: 50px;
  color: #0e0e13 !important;
  padding-left: 54px;
  transition: 0.5s;
  cursor: pointer;
  display: inline-block;
  z-index: 5;
}
.portfolio__button::before, .services__item-detail::before {
  content: "";
  width: 38px;
  height: 38px;
  border-radius: 50%;
  display: block;
  position: absolute;
  left: 3px;
  top: 3px;
  transition: 0.5s;
  background-color: #0e0e13;
  background-image: url(../img/svg/call.svg);
  background-size: 60%;
  background-repeat: no-repeat;
  background-position: center;
}

.gradient-button {
  text-decoration: none;
  display: inline-block;
  padding: 8px 12px;
  border: 1px hidden;
  font-family: "Montserrat", sans-serif;
  letter-spacing: 2px;
  background-image: linear-gradient(to right, #0e0e13 0%, rgba(14, 14, 19, 0.4) 50%, #0e0e13 100%);
  background-size: 220% auto;
  box-shadow: 0 0 5px 2px rgba(14, 14, 19, 0.8);
  transition: 0.5s;
  border-radius: 10px;
}

.gradient-button:hover {
  background-position: right center;
}

.portfolio__mobile-img::after, .portfolio__tablet-img::after, .portfolio__monitor-img::after {
  transition: all 0.5s;
  opacity: 1;
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url(../img/svg/play.svg);
  background-size: 120px;
  background-color: rgba(14, 14, 19, 0.4);
  background-repeat: no-repeat;
  background-position: center;
  z-index: 7;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.ttf") format("truetype");
}
@font-face {
  font-family: "Gruppo";
  src: url("../fonts/Gruppo-Regular.ttf") format("truetype");
}
html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

ul[class],
ol[class] {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  font-size: inherit;
}

ul[class] {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

a {
  text-decoration: none;
}

html,
body {
  height: 100%;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto";
  text-align: justify;
  min-width: 320px;
  -moz-user-select: none;
  -khtml-user-select: none;
  user-select: none;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  overflow-x: hidden;
  background: #0e0e13;
}

.main {
  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}

.container-fluid {
  padding: 0 15px;
  width: 100%;
}

.header {
  position: fixed;
  z-index: 15;
  top: 0;
  width: 100%;
  transition: top 0.5s;
  background-color: #f0b752;
}

.navbar {
  display: grid;
  grid-template-columns: 140px 250px 1fr 67px;
  height: 90px;
  width: 100%;
  position: relative;
  z-index: 10;
  color: #202020;
}
.navbar__logo {
  grid-area: 2/1/3/2;
  margin: auto 10px;
}
.navbar__logo-img {
  height: 70px;
}
.navbar__title {
  grid-area: 1/1/2/5;
  font-family: "Jura";
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-align: center;
}
.navbar__tel {
  grid-area: 2/2/3/3;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #f0b752;
  font-size: 15px;
  white-space: nowrap;
}
.navbar__tel-link {
  border-radius: 10px;
  color: inherit;
  padding: 8px 12px;
  margin: auto;
}
.navbar-btn {
  margin: 10px;
  display: none;
  width: 47px;
  height: 47px;
  padding: 5px;
  cursor: pointer;
  background-color: transparent;
  grid-area: 2/4/3/5;
}
.navbar-btn span {
  display: block;
  width: 30px;
  height: 4px;
  margin: 4px;
}

.menu-list {
  grid-area: 2/3/3/4;
  display: flex;
  justify-content: end;
  align-items: center;
  transition: all 0.5s;
}
.menu-list__item-link {
  font-family: "Jura";
  font-weight: 600;
  font-size: 18px;
  margin: 0 15px;
  text-transform: uppercase;
  white-space: nowrap;
  color: #f0b752;
}

.services {
  margin: 110px auto 0;
}
.services:hover .services__title::after {
  width: 200px;
}
.services__list {
  margin-top: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 15px;
}
.services__item {
  display: grid;
  grid-template-columns: 120px 1fr;
  gap: 10px;
  position: relative;
  color: #c1aece;
  transition: all 0.5s;
  border-radius: 10px;
  background: #202020;
}
.services__item-img {
  background: #f0b752;
  grid-area: 1/1/4/2;
  position: relative;
  z-index: 10;
  border-radius: 10px 0 0 10px;
  min-height: 200px;
}
.services__item-img::after {
  content: "";
  display: block;
  width: 30px;
  height: 30px;
  background: #f0b752;
  position: absolute;
  right: -15px;
  top: calc(50% - 20px);
  transform: rotate(45deg);
  z-index: 9;
}
.services__item-img img {
  height: 120px;
  color: #c1aece;
  object-position: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 11;
}
.services__item-title {
  padding: 10px;
  font-family: "Seymour One", sans-serif;
  color: #c1aece;
  text-align: center;
  grid-area: 1/2/2/3;
  text-transform: uppercase;
}
.services__item-text {
  text-align: start;
  color: #c1aece;
  font-size: 16px;
  line-height: 24px;
  grid-area: 2/2/3/3;
  padding: 0 10px 0 15px;
}
.services__item-box {
  display: flex;
  align-items: center;
  justify-content: space-between;
  grid-area: 3/2/4/3;
}
.services__item-price {
  margin-left: 10px;
  background: transparent;
  border: 1px solid #f0b752;
  color: #f0b752;
  padding: 10px 20px;
  border-radius: 10px;
  text-align: center;
  grid-area: 3/2/4/3;
}
.services__item-detail:hover {
  padding-left: 0px;
}
.services__item-detail:hover::before {
  left: 158px;
}

.stage:hover .stage__title::after {
  width: 300px;
}
.stage__list {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  width: 100%;
  max-width: 1600px;
  margin: 40px auto;
}
.stage__item {
  position: relative;
  color: inherit;
  width: 14.2857142857%;
  height: 80px;
  z-index: 8;
  cursor: pointer;
}
.stage__item:nth-child(1) .stage__link::after {
  content: "";
  width: calc(50% - 14px);
  height: 4px;
  background: #0e0e13;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translatey(-50%);
}
.stage__item:nth-child(7) .stage__link::after {
  content: "";
  width: calc(50% - 14px);
  height: 4px;
  background: #0e0e13;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translatey(-50%);
}
.stage__item:nth-child(even) .stage__item__percent {
  opacity: 0;
}
.stage__link {
  position: relative;
  display: block;
  width: 100%;
  height: 2px;
  background: #f0b752;
  position: absolute;
  bottom: 20px;
  left: 0;
}
.stage__item__point {
  display: block;
  width: 20px;
  height: 20px;
  border: 2px solid #f0b752;
  position: absolute;
  background: #202020;
  top: calc(50% - 10px);
  left: calc(50% - 10px);
  transform: translate(-50%, -50%);
  transform: rotate(45deg);
}
.stage__item__percent {
  opacity: 1;
  color: #c1aece;
  position: absolute;
  text-align: center;
  left: 0;
  right: 0;
  top: 0;
  transition: all 0.5s;
}

.stage__item.stage__item--active .stage__item__point {
  transition: all 0.5s;
  background: #f0b752;
}
.stage__item.stage__item--active .stage__item__percent {
  opacity: 1;
}

.slider {
  position: relative;
  max-width: 1000px;
  margin: 0px auto;
  padding: 0 55px;
}
.slider__item {
  background: #202020;
  border-radius: 10px;
  display: grid;
  grid-template-columns: 120px 1fr;
  gap: 10px;
  color: #c1aece;
}
.slider__item-num {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #f0b752;
  font-family: "Gruppo";
  text-align: center;
  color: #0e0e13;
  border-radius: 10px 0 0 10px;
  font-size: 140px;
  line-height: 180px;
  height: 100%;
  width: 100%;
  grid-area: 1/1/3/2;
}
.slider__item-title {
  font-family: "Seymour One", sans-serif;
  padding: 10px 10px 0 0;
  grid-area: 1/2/2/3;
  text-align: center;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
}
.slider__item-text {
  max-width: 100%;
  padding: 0 10px 10px 0;
  grid-area: 2/2/3/3;
  text-indent: 20px;
}

.slick-list {
  overflow: hidden;
  border-radius: 10px;
}
.slick-track {
  display: flex;
}
.slick-arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 10;
  font-size: 0;
  transition: all 0.3s;
}
.slick-arrow.slick-prev {
  left: 0;
  background: url(../img/svg/left-arrow.svg) 0 0/100% no-repeat;
  width: 40px;
  height: 40px;
  border: hidden;
}
.slick-arrow.slick-prev:hover {
  left: -5px;
}
.slick-arrow.slick-next {
  background: url(../img/svg/right-arrow.svg) 0 0/100% no-repeat;
  right: 0;
  width: 40px;
  height: 40px;
  border: hidden;
}
.slick-arrow.slick-next:hover {
  right: -5px;
}

.portfolio:hover .portfolio__title::after {
  width: 200px;
}
.portfolio__inner {
  background: #f0b752;
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 0;
  padding: 15px;
}
.portfolio__item {
  position: relative;
  overflow: hidden;
}
.portfolio__item:hover {
  z-index: 10;
}
.portfolio__button {
  grid-area: 2/1/3/8;
  width: 220px;
  margin: 30px auto;
}
.portfolio__button::before {
  background-image: url(../img/svg/arrow_right.svg);
}
.portfolio__button:hover {
  padding-left: 0px;
}
.portfolio__button:hover::before {
  left: 175px;
}
.portfolio__monitor {
  grid-area: 1/1/2/8;
  align-items: start;
  margin: 0px 0px auto 0px;
  max-width: 600px;
  width: 600px;
  min-width: 270px;
  max-height: 450px;
  z-index: 2;
  aspect-ratio: 4/3;
}
.portfolio .portfolio__monitor:hover .portfolio__monitor-img::after {
  background-color: transparent;
}
.portfolio__samsung {
  position: absolute;
  overflow: hidden;
  z-index: 2;
}
.portfolio__monitor-img {
  position: absolute;
  padding: 3.5% 4% 4% 4%;
  width: 100%;
  overflow: hidden;
  aspect-ratio: 367/200;
}
.portfolio__tablet {
  grid-area: 1/4/2/7;
  max-width: 540px;
  min-width: 270px;
  width: 540px;
  max-height: 358px;
  aspect-ratio: 270/179;
  margin: auto 0px 0px 0px;
}
.portfolio .portfolio__tablet:hover .portfolio__tablet-img::after {
  background-color: transparent;
}
.portfolio__ipad {
  position: absolute;
  overflow: hidden;
  z-index: 6;
}
.portfolio__tablet-img {
  padding: 3.6% 10.5%;
  position: absolute;
  width: 100%;
  overflow: hidden;
  aspect-ratio: 426/280;
  z-index: 5;
}
.portfolio__tablet-img::after {
  width: 85%;
  height: 93%;
}
.portfolio__mobile {
  grid-area: 1/6/2/8;
  margin: 0 0 0 auto;
  width: 300px;
  min-width: 270px;
  height: 600px;
  aspect-ratio: 2/1;
}
.portfolio .portfolio__mobile:hover .portfolio__mobile-img::after {
  background-color: transparent;
}
.portfolio__iphone {
  position: absolute;
  overflow: hidden;
  z-index: 4;
}
.portfolio__mobile-img {
  padding: 4% 4%;
  position: absolute;
  width: 100%;
  overflow: hidden;
  aspect-ratio: 270/528;
  z-index: 3;
  border-radius: 35px;
}
.portfolio__mobile-img::after {
  padding: 4% 4%;
}
.portfolio__mobile-img img {
  border-radius: 35px;
}
@keyframes scroll {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(calc(-100% + var(--he)));
  }
  100% {
    transform: translateY(0);
  }
}
.portfolio__item--scroll.portfolio__monitor .portfolio__monitor-img::after {
  opacity: 0;
}
.portfolio__item--scroll.portfolio__monitor .portfolio__monitor-img img {
  animation: scroll 20s ease-in-out forwards;
}
.portfolio__item--scroll.portfolio__tablet .portfolio__tablet-img::after {
  opacity: 0;
}
.portfolio__item--scroll.portfolio__tablet .portfolio__tablet-img img {
  animation: scroll 20s ease-in-out forwards;
}
.portfolio__item--scroll.portfolio__mobile .portfolio__mobile-img::after {
  opacity: 0;
}
.portfolio__item--scroll.portfolio__mobile .portfolio__mobile-img img {
  animation: scroll 20s ease-in-out forwards;
}

.contacts:hover .contacts-block__title::after {
  width: 200px;
}

.contacts-block {
  margin-bottom: 20px;
  display: grid;
  grid-template-columns: 1fr 3fr;
  gap: 15px;
  color: #c1aece;
}
.contacts-block__address {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #f0b752;
  border-radius: 10px;
  text-align: center;
  margin-bottom: 10px;
  cursor: pointer;
  background-color: #202020;
  padding: 20px;
}
.contacts-block__onmap iframe {
  width: 100%;
  height: 300px;
  border-radius: 10px;
}

.footer {
  background-color: #f0b752;
}
.footer__logo img {
  margin: auto;
  height: 60px;
}
.footer__inner {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: 10px auto;
  padding: 0 15px;
  color: #f0b752;
}
.footer-contacts {
  font-family: "Jura";
}
.footer-contacts__list-item {
  margin: 10px 20px;
  font-size: 16px;
  line-height: 24px;
}
.footer-contacts__list-item a {
  color: inherit;
}
.footer-social__list {
  display: flex;
  flex-wrap: nowrap;
  justify-content: end;
}
.footer-social__list-item {
  font-size: 32px;
  line-height: 32px;
  margin: 15px;
  border-radius: 10px;
  padding: 8px;
}
.footer-social__list-item a {
  color: inherit;
}

@media (max-width: 1500px) {
  .services__list {
    grid-template-columns: 1fr 1fr;
  }
  .portfolio__tablet {
    grid-area: 1/3/2/7;
  }
}
@media (max-width: 1200px) {
  .portfolio__tablet {
    grid-area: 1/3/2/8;
  }
}
@media (max-width: 992px) {
  .services__list {
    grid-template-columns: 1fr;
  }
  .portfolio__monitor {
    grid-area: 1/1/2/8;
  }
  .portfolio__tablet {
    grid-area: 2/1/3/8;
  }
  .portfolio__mobile {
    grid-area: 2/5/3/8;
  }
  .portfolio__button {
    grid-area: 3/4/4/6;
  }
}
@media (max-width: 850px) {
  .navbar {
    position: relative;
  }
  .navbar-btn {
    position: relative;
    display: block;
    height: 47px;
    padding: 5px;
    cursor: pointer;
    border-radius: 10px;
  }
  .navbar-btn span {
    display: block;
    width: 30px;
    height: 4px;
    background-color: #f0b752;
    margin: 6px 3px;
  }
  .navbar-btn.navbar-btn--active span {
    transition: all 0.5s;
  }
  .navbar-btn.navbar-btn--active span:nth-child(1) {
    position: absolute;
    top: 16px;
    transform: rotate(45deg);
  }
  .navbar-btn.navbar-btn--active span:nth-child(2) {
    opacity: 0;
  }
  .navbar-btn.navbar-btn--active span:nth-child(3) {
    position: absolute;
    bottom: 16px;
    transform: rotate(-45deg);
  }
  .menu-list {
    grid-area: 2/1/3/5;
    margin-right: 0;
    border: 1px solid #f0b752;
    border-radius: 0 0 10px 10px;
    display: flex;
    flex-wrap: wrap;
    background: #f0b752;
    position: absolute;
    z-index: 12;
    right: 0;
    left: 0;
    top: -350px;
  }
  .menu-list__item {
    text-align: center;
    margin: 10px 20px;
    width: 100%;
  }
  .menu-list__item-link {
    margin: 0 auto;
