    z-index: 12;
  }
  .services-block {
    grid-template-columns: 1fr 1fr;
  }
  .portfolio__mobile {
    grid-area: 2/5/3/8;
  }
  .portfolio__button {
    grid-area: 3/1/4/8;
  }
}
@media (max-width: 768px) {
  .portfolio__monitor {
    width: calc(100vw - 30px);
    margin: auto;
  }
  .portfolio__tablet {
    grid-area: 2/1/3/8;
    width: calc(100vw - 30px);
    margin: 20px auto;
  }
  .portfolio__mobile {
    grid-area: 3/1/4/8;
    margin: auto;
  }
  .portfolio__button {
    grid-area: 4/1/5/8;
    margin: 20px auto;
  }
}
