    grid-template-columns: 120px 170px 1fr 67px;
    margin: auto;
  }
  .navbar__title {
    font-size: 14px;
    line-height: 20px;
  }
  .navbar__tel {
    font-size: 14px;
    letter-spacing: 0px;
    height: min-content;
    line-height: 18px;
    text-align: center;
    margin: auto 0;
    padding: 5px 10px;
  }
  .slider {
    padding: 0px;
  }
  .slider__item {
    grid-template-columns: 80px 1fr;
  }
  .slider__item-num {
    font-size: 100px;
  }
  .slick-list {
    margin-bottom: 100px;
  }
  .slick-arrow {
    top: unset;
    bottom: -80px;
  }
  .slick-arrow.slick-prev {
    left: 80px;
  }
  .slick-arrow.slick-prev:hover {
    left: 75px;
  }
  .slick-arrow.slick-next {
    right: 80px;
  }
  .slick-arrow.slick-next:hover {
    right: 75px;
  }
  .services__item {
    grid-template-columns: 80px 1fr;
  }
  .services__item-img {
    padding: 10px;
  }
  .services__item-img img {
    height: 60px;
  }
  .services__item-img::after {
    width: 24px;
    height: 24px;
    right: -12px;
  }
  .portfolio__monitor {
    width: calc(100vw - 30px);
  }
  .portfolio__tablet {
    grid-area: 2/1/3/8;
  }
  .portfolio__mobile {
    grid-area: 3/1/4/8;
  }
  .contacts-block {
    grid-template-columns: 1fr;
  }
  .footer-social__list-item {
    font-size: 24px;
    line-height: 24px;
  }
  .footer-contacts__list-item {
    font-size: 14px;
    line-height: 20px;
  }
}
@media (max-width: 540px) {
  .footer-contacts__list-item {
    margin: 10px 0;
    font-size: 14px;
    line-height: 20px;
  }
}
@media (max-width: 480px) {
  .slider {
    position: relative;
    max-width: 1000px;
  }
  .slider__item-num {
    font-size: 80px;
    line-height: 80px;
    height: 80px;
    border-radius: 10px 0 0 0;
    grid-area: 1/1/2/2;
  }
  .slider__item-title {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 10px 0 0;
    grid-area: 1/2/2/3;
    text-align: center;
    text-transform: uppercase;
    height: 80px;
  }
  .slider__item-text {
    max-width: 100%;
    padding: 0 10px 10px 10px;
    grid-area: 2/1/3/3;
    text-indent: 20px;
  }
  .services__item-img {
    grid-area: 1/1/2/2;
    min-height: 80px;
    border-radius: 10px 0 0 0;
  }
  .services__item-text {
    grid-area: 2/1/3/3;
    word-break: normal;
  }
  .services__item-title {
    margin: auto;
    font-size: 14px;
  }
  .services__item-box {
    display: flex;
    align-items: center;
    justify-content: space-between;
    grid-area: 3/1/4/3;
  }
  .services__item-price {
    grid-area: 3/1/4/3;
  }
  .services__item-detail {
    margin: 10px;
    min-width: unset;
    width: 160px;
    z-index: 5;
  }
  .services__item-detail:hover::before {
    left: 118px;
  }
  .navbar__title {
    font-size: 13px;
    line-height: 18px;
    overflow-y: hidden;
  }
  .portfolio__monitor {
    --he: 200px;
  }
  .portfolio__tablet {
    --he: 240px;
  }
}